<template>
  <div>
    <ul class="list">
      <li v-for="post in posts">
        <div class="avastar"><img :src="post.author_avastar" alt="avastar"></div>
        <div class="title">
          <router-link :to="'/post/'+post.id">{{post.title}}</router-link>
        </div>
        <div class="topic_info">
          <div class="author">发帖人: <strong><a href="javascript:void(0);">{{post.author_name}}</a></strong></div>
          <div class="timestamp">时间: <span>{{formatTime(post.timestamp)}}</span></div>
        </div>
        <div class="reply_count">{{post.comment_count}}</div>
      </li>
    </ul>
    <section class="pages-wrap" v-show="count>pagenum">
      <ul class="pagination clearfix">
        <li :class="{'disabled':prev==null}"><a href="javascript:;" @click="getPosts(baseurl+'/api/v1/posts/')"> 首页 </a>
        </li>
        <li :class="{'disabled': count<pagenum || prev==null}"><a href="javascript:;"
                                                                  @click="getPosts(baseurl+prev)"> 上一页 </a></li>
        <li :class="{'disabled': count<pagenum || next==null}"><a href="javascript:;"
                                                                  @click="getPosts(baseurl+next)"> 下一页 </a></li>
      </ul>
    </section>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "post-list",
    data() {
      return {
        baseurl: 'https://www.vetoer.club',
        pagenum: 20,
        prev: null,
        next: null,
        count: 0,
        posts: [],
      };
    },
    props: {
      city: {
        type: String,
        default: 'sz'
      }
    },
    mounted: function () {
      this.getPosts(this.baseurl + '/api/v1/' + this.city + '/posts/');
      // this.getPosts(this.baseurl + '/api/v1/posts/');
    },
    watch: {
      city: function () {
        console.log('city change: ' + this.city);
        this.getPosts(this.baseurl + '/api/v1/' + this.city + '/posts/');
      }
    },
    methods: {
      getPosts(url) {
        axios.get(url).then((res) => {
          console.log(res.data);
          this.prev = res.data.prev;
          this.next = res.data.next;
          this.posts = res.data.posts;
          this.count = res.data.count;
        }).catch((err) => {

        });
        window.scrollTo({"behavior": "smooth", "top": 10});   // 跳回顶部
      },
      clickTitle(post_id) {
        this.$emit('toPostDetail', post_id);
      },
      formatTime(timestamp) {
        let ti = new Date(timestamp);
        return ti.toLocaleString();
      },
    }
  }
</script>

<style scoped>


  .pages-wrap {
    text-align: center;
  }

  .pagination {
    display: inline-block;
    padding-left: 0;
    margin: 20px 0;
    border-radius: 4px;
  }

  .pagination > li {
    display: inline;
  }

  .pagination > .active > a, .pagination > .active > a:hover, .pagination > .active > span, .pagination > .active > span:hover {
    z-index: 3;
    color: #fff;
    cursor: default;
    background-color: #337ab7;
    border-color: #337ab7;
  }

  .pagination > .disabled > a, .pagination > .disabled > a:hover, .pagination > .disabled > span, .pagination > .disabled > span:hover {
    color: #777;
    cursor: not-allowed;
    background-color: #fff;
    border-color: #ddd;
  }

  .pagination > li > a:hover, .pagination > li > span:hover {
    z-index: 2;
    color: rgba(0, 0, 0, 0.6);
    background-color: #eee;
    border-color: #ddd;
  }

  .pagination > li > a, .pagination > li > span {
    position: relative;
    float: left;
    padding: 6px 12px;
    margin-left: -1px;
    line-height: 1.42857143;
    color: #000;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #ddd;
  }

  .list {
    list-style: none;
    width: 100%;
  }

  .list > li {
    padding: 10px;
    font-size: 14px;
    line-height: 120%;
    text-align: left;
    border-bottom: 1px solid #e2e2e2;
    overflow: hidden;
    position: relative;
  }

  .avastar {
    display: inline-block;
  }

  .avastar > img {
    max-width: 48px;
    max-height: 48px;
  }

  .title {
    position: absolute;
    top: 10px;
    left: 65px;
    line-height: 130%;
    text-shadow: 0 1px 0 #fff;
    word-wrap: break-word;
    max-width: 80%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .title > a {
    color: #778087;
    text-decoration: none;
    word-break: break-all;
    font-size: 16px;
  }

  .title > a:hover, a:hover {
    color: #4d5256;
    text-decoration: underline;
  }

  .topic_info {
    font-size: 12px;
    position: absolute;
    top: 60%;
    left: 65px;
  }

  .topic_info > * {
    float: left;
    padding-right: 10px;
  }

  .reply_count {
    position: absolute;
    right: 10px;
    top: 45%;
    font-weight: 700;
    border-radius: 12px;
    text-decoration: none;
    line-height: 12px;
    color: #fff;
    background-color: #aab0c6;
    display: inline-block;
    padding: 2px 10px 2px 10px;
    margin-right: 5px;
    word-break: keep-all;
  }

</style>



